.inputWrapper {
  display: flex;
  flex-direction: column;
}

.input {
  width: min(100vw, 372px);
  height: 36px;
  border: 2px solid;
  border-radius: var(--border-radius-sm);
  outline: none;
}

.superInput {
  border-color: var(--grey-600);
}

.input:focus {
  border-color: var(--blue-400);
}

.errorInput {
  border-color: var(--red-400);
}

.errorInput:focus {
  border-color: var(--red-400);
}

.error {
  margin-bottom: 4px;
  padding: 0 4px;
  font-size: 12px;
  color: var(--red-400);
}
