.button {
  padding: 8px 24px;
  height: 36px;
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: outline-width 0.15s ease-in-out;
  text-transform: capitalize;
}

.default {
  --bg-color: var(--blue-400);
  color: var(--white);
  background-color: var(--bg-color);
}

.default:hover {
  --adjust-bg-color: var(--blue-500);
  background-color: var(--adjust-bg-color);
}

.default:active {
  --adjust-bg-color: var(--blue-600);
  background-color: var(--adjust-bg-color);
}

.red {
  --bg-color: var(--red-400);
  color: var(--white);
  background-color: var(--bg-color);
}

.red:hover {
  --adjust-bg-color: var(--red-500);
  background-color: var(--adjust-bg-color);
}

.red:active {
  --adjust-bg-color: var(--red-600);
  background-color: var(--adjust-bg-color);
}

.secondary {
  --bg-color: var(--white);
  border: 2px solid var(--blue-400);
  background-color: var(--bg-color);
  color: var(--blue-400);
}

.secondary:hover {
  --adjust-bg-color: var(--blue-200);
  background-color: var(--adjust-bg-color);
}

.secondary:active {
  --adjust-bg-color: var(--blue-300);
  background-color: var(--adjust-bg-color);
}

.button.disabled {
  --adjust-bg-color: var(--bg-color);
  filter: grayscale(30%);
  opacity: 0.5;
  cursor: initial;
}
